export default {
	name: "tuiListCell",
	props: {
		arrow: {
			type: Boolean,
			default: false //是否有箭头
		},
		hover: {
			type: Boolean,
			default: true //是否有点击效果
		},
		last: {
			type: Boolean,
			default: false //最后一条数据隐藏线条
		},
		bgcolor: {
			type: String,
			default: "#fff" //背景颜色
		},
		size: {
			type: Number,
			default: 32 //字体大小
		},
		color: {
			type: String,
			default: "#333" //字体颜色
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClick() {
			this.$emit('click', {
				index: this.index
			});
		},
		px: function(num) {
			return uni.upx2px(num) + 'px';
		}
	}
}
